@keyframes vibrate {
  0% {
    transform: translate(0);
  }

  20% {
    transform: translate(-2px, 2px);
  }

  40% {
    transform: translate(-2px, -2px);
  }

  60% {
    transform: translate(2px, 2px);
  }

  80% {
    transform: translate(2px, -2px);
  }

  100% {
    transform: translate(0);
  }
}

@keyframes turnCarLeft_animation {
  1% {
    opacity: 1;
    top: 80%;
    left: 30%;
  }

  98% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    top: 85%;
    left: 5%;
  }
}

@keyframes turnLeft {
  10% {
    opacity: 1;
    left: 15%;
  }

  90% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    left: 0%;
  }
}

@keyframes turnLeft_animation {
  10% {
    opacity: 1;
    right: 20%;
  }

  25% {
    opacity: 1;
    top: 67%;
    right: 25%;
    transform: scale(-.6, .6);
  }

  50% {
    opacity: 1;
    top: 62%;
    right: 30%;
    transform: scale(-.4, .4);
  }

  75% {
    opacity: 0;
    top: 60%;
    right: 45%;
    transform: scale(-.3, .3);
  }

  100% {
    opacity: 0;
    top: 58%;
    right: 55%;
    transform: scale(-.2, .2);
  }
}

@keyframes turnForward_animation {
  10% {
    opacity: .2;
    bottom: 34%;
    left: 28%;
    transform: scale(-.2, .2);
  }

  30% {
    opacity: .5;
    bottom: 32%;
    left: 25%;
    transform: scale(-.9, .9);
  }

  50% {
    opacity: .6;
    bottom: 20%;
    left: 24%;
    transform: scale(-1, 1);
  }

  75% {
    opacity: .8;
    bottom: 15%;
    left: 23.5%;
    transform: scale(-1.3, 1.3);
  }

  100% {
    opacity: 1;
    bottom: 8%;
    left: 21%;
    transform: scale(-2.2, 2.2);
  }
}

